<template>
        <div class="choose choose-h5">
                <h1 class="title">
                        <span class="swiperBtn prev"><i class="iconfont icon-fanhui"></i></span>
                        {{ t(WHY_CHOOSE_MFT.title) }}
                        <span class="swiperBtn next"><i class="iconfont icon-fanhui"></i></span>
                </h1>
                <div class="chooseSwiper">
                        <div class="swiperContainer">
                                <Swiper :modules="[SwiperNavigation]" :navigation="{
                                        nextEl: '.swiperBtn.next',
                                        prevEl: '.swiperBtn.prev',
                                }" :slides-per-view="swiperView" :space-between="spaceBetween" :loop="true" :centeredSlides='isMobile'>
                                        <SwiperSlide v-for="(slide, idx) in slideList" :key="idx">
                                                <nuxt-link-locale :to="slide.url" class="chooseItem"
                                                        :style="{ backgroundColor: '#28292C' }" v-if="idx !== (slideList.length - 1)">
                                                        <div class="textCon">
                                                                <img :src="slide.pic" class="pic"/>
                                                                <p class="desc">{{ t(slide.name) }}</p>

                                                        </div>
                                                        <p class="more" >
                                                                {{ t("Education.more") }}<i class="iconfont icon-arrow-right"></i>
                                                        </p>
                                                </nuxt-link-locale>
                                                <div class="chooseItem" @click="linkTo(slide.url)"
                                                        :style="{ backgroundColor: '#28292C' }" v-else>
                                                        <div class="textCon">
                                                                <img :src="slide.pic" class="pic"/>
                                                                <p class="desc">{{ t(slide.name) }}</p>

                                                        </div>
                                                        <p class="more" >
                                                                {{ t("Education.more") }}<i class="iconfont icon-arrow-right"></i>
                                                        </p>
                                                </div>
                                        </SwiperSlide>
                                </Swiper>
                                <div class="swiperBtn prev"><i class="iconfont icon-fanhui"></i></div>
                                <div class="swiperBtn next"><i class="iconfont icon-fanhui"></i></div>
                        </div>
                </div>
        </div>
        <div class="choose">
                <h1 class="title">{{ t(WHY_CHOOSE_MFT.title) }}</h1>
                <div class="choose-list">
                        <nuxt-link-locale :to="item.url" class="choose-list-item" v-for="(item, idx) in WHY_CHOOSE_MFT.children" :key="idx">
                                <div>
                                        <el-image :src="item.pic" lazy class="choose-list-item__pic"/>
                                        <div class="choose-list-item__txt">{{t(item.name)}}</div>
                                </div>
                                <div class="choose-list-item__more">{{t("Education.more") }}<i class="iconfont icon-arrow-right"></i></div>
                        </nuxt-link-locale>
                </div>
        </div>
</template>
<script setup>
import { WHY_CHOOSE_MFT } from '@/data'
import { useCheckMobile } from '@/store/isMobile'
const router = useRouter()
const { t } = useI18n();
const slideList = ref([])
const spaceBetween = ref(0)
const swiperView = ref(0)
const isMobile = computed(() => useCheckMobile().getItem())
const setSwiperOptions = () => {
        spaceBetween.value = isMobile.value ? (window.innerWidth / 375) * 20 : (window.innerWidth / 1920) * 40
        if (window.innerWidth >= 1440) {
                swiperView.value = 5.15
        } else if (window.innerWidth >= 1200 && window.innerWidth < 1440) {
                swiperView.value = 4.15
        } else if (window.innerWidth >= 750 && window.innerWidth < 1200) {
                swiperView.value = 3.5
        } else {
                swiperView.value = 1.75
        }
};
const linkTo = (url) => {
        if(url.includes("discord")) {
                window.open(url)
        }else {
                router.push(url)
        }
}
onMounted(() => {
        slideList.value = [...WHY_CHOOSE_MFT.children]
        setSwiperOptions()
        window.addEventListener("resize", _debounce(setSwiperOptions, 500));
});
onUnmounted(() => {
        window.removeEventListener("resize", _debounce(setSwiperOptions, 500));
});
</script>
<style scoped lang="scss" src="./index.scss"></style>